html{
	height: 100%;
}
body { 
	text-align: center; 
	padding: 10%; 
	font: 20px Helvetica, sans-serif; 
	color: #333; 
	background: transparent url('../images/pic.jpg') center center no-repeat; 
	background-size: cover;
}
h1 { 
	font-size: 50px; 
	margin: 0; 
}
article { 
	display: block; 
	text-align: left; 
	width: 68%; 
	text-align: center;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translateY(-50%) translateX(-50%);
}
a { 
	color: #dc8100; 
	text-decoration: none; 
}
a:hover { 
	color: #333; 
	text-decoration: none; 
}
@media only screen and (max-width : 480px) {
	h1 { font-size: 40px; }
}
